@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/* Root variables */
:root {
  --primary-color: #e50914;
  --primary-hover: #b81d24;
  --secondary-color: #221f1f;
  --text-color: #f5f5f1;
  --background-dark: #141414;
  --background-light: #f5f5f1;
}

/* Custom styles */
body {
  font-family: 'Inter', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  @apply transition-colors duration-300;
}

/* Dark mode */
.dark {
  @apply bg-gray-900 text-white;
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

@keyframes pulse {
  0% { opacity: 1; }
  50% { opacity: 0.5; }
  100% { opacity: 1; }
}

@keyframes scale {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.animate-fadeIn {
  animation: fadeIn 0.5s ease-in-out;
}

.animate-slideUp {
  animation: slideUp 0.5s ease-out;
}

.animate-pulse {
  animation: pulse 2s infinite ease-in-out;
}

.animate-scale {
  animation: scale 2s infinite ease-in-out;
}

.animate-bounce {
  animation: bounce 2s infinite ease-in-out;
}

/* Movie card hover effects */
.card {
  transition: transform 0.3s ease, box-shadow 0.3s ease, opacity 0.3s ease;
  @apply bg-white dark:bg-gray-800 overflow-hidden;
}

.card:hover {
  transform: translateY(-8px);
  @apply shadow-xl;
}

/* Custom button styles */
.btn-primary {
  @apply bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded transition duration-200 inline-flex items-center justify-center;
}

.btn-secondary {
  @apply bg-gray-600 hover:bg-gray-700 text-white font-medium py-2 px-4 rounded transition duration-200 inline-flex items-center justify-center;
}

.btn-outline {
  @apply border border-red-600 text-red-600 hover:bg-red-600 hover:text-white font-medium py-2 px-4 rounded transition duration-200 inline-flex items-center justify-center;
}

.btn-danger {
  @apply bg-red-600 hover:bg-red-700 text-white font-medium py-2 px-4 rounded transition duration-200 inline-flex items-center justify-center;
}

.btn-success {
  @apply bg-green-600 hover:bg-green-700 text-white font-medium py-2 px-4 rounded transition duration-200 inline-flex items-center justify-center;
}

/* Form controls */
.form-input {
  @apply border border-gray-300 dark:border-gray-700 rounded-md py-2 px-3 w-full focus:outline-none focus:ring-2 focus:ring-red-500 dark:bg-gray-800 dark:text-white transition duration-200;
}

.form-select {
  @apply border border-gray-300 dark:border-gray-700 rounded-md py-2 px-3 w-full focus:outline-none focus:ring-2 focus:ring-red-500 dark:bg-gray-800 dark:text-white transition duration-200;
}

.form-label {
  @apply block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1;
}

.form-checkbox {
  @apply rounded border-gray-300 text-red-600 focus:ring-red-500 transition duration-200;
}

/* Movie element styles */
.movie-title {
  @apply font-bold text-lg md:text-xl text-gray-900 dark:text-white;
}

.movie-subtitle {
  @apply text-sm text-gray-600 dark:text-gray-400;
}

.movie-description {
  @apply text-gray-700 dark:text-gray-300 leading-relaxed;
}

/* Category badges */
.category-badge {
  @apply inline-block bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-gray-300 text-xs px-2.5 py-1 rounded-full mr-2 mb-2 transition-colors duration-200;
}

.rating-badge {
  @apply inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200;
}

/* Gradient texts */
.gradient-text {
  @apply bg-clip-text text-transparent bg-gradient-to-r from-red-500 to-yellow-500;
}

.gradient-text-blue {
  @apply bg-clip-text text-transparent bg-gradient-to-r from-blue-500 to-purple-500;
}

/* Custom loader animation */
@keyframes spin {
  to { transform: rotate(360deg); }
}

.loader {
  @apply w-6 h-6 border-2 border-gray-300 border-t-red-600 rounded-full;
  animation: spin 1s linear infinite;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  @apply bg-gray-100 dark:bg-gray-800 rounded-full;
}

::-webkit-scrollbar-thumb {
  @apply bg-gray-400 dark:bg-gray-600 rounded-full;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-red-500 dark:bg-red-600;
}

/* Layout utilities */
.container {
  @apply px-4 mx-auto max-w-screen-xl;
}

/* Hero section */
.hero-section {
  @apply relative bg-gradient-to-r from-gray-900 to-black text-white overflow-hidden;
  min-height: 70vh;
}

.hero-content {
  @apply relative z-10 py-16 md:py-24 container;
}

.hero-title {
  @apply text-4xl md:text-6xl font-bold mb-4 leading-tight;
}

.hero-subtitle {
  @apply text-xl md:text-2xl text-gray-300 mb-8 max-w-2xl;
}

/* Grid layouts */
.movie-grid {
  @apply grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-4 md:gap-6;
}

.series-grid {
  @apply grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6;
} 